export default {
	props: {
		listItem: Object
	},
	methods: {
		handleItemClick() {
			if (this.listItem.navigatorUrl != null) {
				if (this.listItem.navigatorType == 'navigatorTo') {
					uni.navigateTo({
						url: this.listItem.navigatorUrl
					})
				} else if (this.listItem.navigatorType == 'switchTab') {
					uni.switchTab({
						url: this.listItem.navigatorUrl
					})
				} else if (this.listItem.navigatorType == 'clear') {
					uni.showModal({
						title: "提示",
						content: "是否要清除缓存",
						confirmText: "立即清除",
						success: (res) => {
							if (res.confirm) {
								uni.clearStorage()
								uni.showToast({
									title: "清除成功",
									icon: "none"
								})
								uni.navigateBack({
									delta: 1
								})
							}
						}
					})
				}
			}
		}
	}
}
